import React, { useEffect, useState } from 'react'
import { FaCheckCircle, FaExclamationTriangle } from 'react-icons/fa'

const SystemCheckCard: React.FC = () => {
  const [isOnline, setIsOnline] = useState<boolean>(navigator.onLine)
  const [storageAvailable, setStorageAvailable] = useState<boolean>(false)

  useEffect(() => {
    const testLocalStorage = () => {
      try {
        const testKey = '__storage_test__'
        localStorage.setItem(testKey, testKey)
        localStorage.removeItem(testKey)
        setStorageAvailable(true)
      } catch {
        setStorageAvailable(false)
      }
    }

    const handleConnectionChange = () => setIsOnline(navigator.onLine)

    testLocalStorage()
    window.addEventListener('online', handleConnectionChange)
    window.addEventListener('offline', handleConnectionChange)

    return () => {
      window.removeEventListener('online', handleConnectionChange)
      window.removeEventListener('offline', handleConnectionChange)
    }
  }, [])

  const systemItems = [
    {
      key: 'network',
      label: '‡∏Å‡∏≤‡∏£‡πÄ‡∏ä‡∏∑‡πà‡∏≠‡∏°‡∏ï‡πà‡∏≠‡∏≠‡∏¥‡∏ô‡πÄ‡∏ó‡∏≠‡∏£‡πå‡πÄ‡∏ô‡πá‡∏ï',
      status: isOnline,
      successLabel: '‡∏≠‡∏≠‡∏ô‡πÑ‡∏•‡∏ô‡πå',
      failLabel: '‡∏≠‡∏≠‡∏ü‡πÑ‡∏•‡∏ô‡πå',
      icon: (ok: boolean) =>
        ok ? (
          <FaCheckCircle
            className="text-2xl text-green-600"
            aria-label="‡∏≠‡∏≠‡∏ô‡πÑ‡∏•‡∏ô‡πå"
            role="img"
            aria-hidden={false}
          />
        ) : (
          <FaExclamationTriangle
            className="text-2xl text-red-600"
            aria-label="‡∏≠‡∏≠‡∏ü‡πÑ‡∏•‡∏ô‡πå"
            role="img"
            aria-hidden={false}
          />
        ),
    },
    {
      key: 'storage',
      label: 'LocalStorage',
      status: storageAvailable,
      successLabel: '‡∏û‡∏£‡πâ‡∏≠‡∏°‡πÉ‡∏ä‡πâ‡∏á‡∏≤‡∏ô',
      failLabel: '‡πÑ‡∏°‡πà‡∏û‡∏£‡πâ‡∏≠‡∏°‡πÉ‡∏ä‡πâ‡∏á‡∏≤‡∏ô',
      icon: (ok: boolean) =>
        ok ? (
          <FaCheckCircle
            className="text-2xl text-green-600"
            aria-label="LocalStorage ‡∏û‡∏£‡πâ‡∏≠‡∏°‡πÉ‡∏ä‡πâ‡∏á‡∏≤‡∏ô"
            role="img"
            aria-hidden={false}
          />
        ) : (
          <FaExclamationTriangle
            className="text-2xl text-red-600"
            aria-label="LocalStorage ‡πÑ‡∏°‡πà‡∏û‡∏£‡πâ‡∏≠‡∏°‡πÉ‡∏ä‡πâ‡∏á‡∏≤‡∏ô"
            role="img"
            aria-hidden={false}
          />
        ),
    },
  ]

  return (
    <section
      aria-label="‡∏™‡∏ñ‡∏≤‡∏ô‡∏∞‡∏Å‡∏≤‡∏£‡∏ï‡∏£‡∏ß‡∏à‡∏™‡∏≠‡∏ö‡∏£‡∏∞‡∏ö‡∏ö"
      tabIndex={-1}
      role="region"
      className="dark:border-base-700 space-y-6 rounded-xl border border-base-300 bg-base-100 p-6 shadow-md transition-shadow duration-200 ease-in-out focus-within:shadow-lg dark:bg-zinc-800"
    >
      <h3 className="select-none text-xl font-bold text-primary">‡∏™‡∏ñ‡∏≤‡∏ô‡∏∞‡∏£‡∏∞‡∏ö‡∏ö</h3>
      <ul className="grid gap-6 sm:grid-cols-2" role="list">
        {systemItems.map(({ key, label, status, successLabel, failLabel, icon }) => (
          <li
            key={key}
            className="flex select-text items-center gap-4"
            role="listitem"
            aria-live="polite"
            aria-atomic="true"
          >
            <span aria-label={`${label}: ${status ? successLabel : failLabel}`} role="img">
              {icon(status)}
            </span>
            <div className="font-medium text-base-content">
              {label}:{' '}
              <span className={`font-semibold ${status ? 'text-green-700' : 'text-red-600'}`}>
                {status ? successLabel : failLabel}
              </span>
            </div>
          </li>
        ))}
      </ul>
    </section>
  )
}

export default SystemCheckCard
